    checkApplicantInfos: function checkApplicantInfos() {
      var vm = this;

      if (vm.originality === 3 && vm.thongTinChuHoSo.userType === '2' && vm.thongTinChuHoSo.applicantIdNo || vm.originality === 1 && vm.thongTinChuHoSo.applicantIdType === 'business') {
        var filter = {
          applicantIdNo: vm.thongTinChuHoSo.applicantIdNo,
          applicantName: vm.thongTinChuHoSo.applicantName // vm.loadingVerify = true

        };
        vm.$store.dispatch('checkApplicantInfos', filter).then(function (result) {
          // vm.loadingVerify = false
          if (result && result.hasOwnProperty('error') && result.error === true) {
            vm.bussinessExits = false;
            vm.validBussinessInfos = false; // vm.$store.commit('setApplicantBussinessExit', false)

            vm.messageCheckApplicant = result.message; // toastr.error(result.message + ' Vui lòng kiểm tra lại mã số thuế')
          } else if (result && result.hasOwnProperty('warning') && result.warning === true) {
            vm.bussinessExits = true;
            vm.validBussinessInfos = false; // vm.$store.commit('setApplicantBussinessExit', false)

            vm.messageCheckApplicant = result.message; // toastr.error(result.message + ' Vui lòng đối chiếu thông tin doanh nghiệp')
          } else if (result && !result.hasOwnProperty('error') && !result.hasOwnProperty('warning')) {
            vm.bussinessExits = true;
            vm.validBussinessInfos = true; // vm.$store.commit('setApplicantBussinessExit', filter['applicantIdNo'])
          }
        }).catch(function () {
          vm.loadingVerify = false;
        });
      }
    },
