      var vm = this;
      vm.dialog_editApplicant = false;
      vm.getApplicantList().then(function (result) {
        vm.totalApplicantSearch = result['total'];
        vm.applicantLists = result['data'];
      }).catch(function () {
        toastr.error('Không lấy được danh sách công dân, tổ chức, doanh nghiệp');
      });
    },
    fullAddress: function fullAddress(address, city, district, ward) {
      var fullAddress = '';

      if (address) {
        fullAddress += address + ', ';
      }

      if (ward) {
        fullAddress += ward + ' - ';
      }

      if (district) {
        fullAddress += district + ' - ';
      }

      if (city) {
        fullAddress += city;
      }

      return fullAddress;
    }
