            toastr.clear();
            toastr.error('Yêu cầu của bạn thực hiện thất bại.');
          }); // 

          vm.updateNotifyConfig();
        }
      } else {
        toastr.error('Vui lòng điền đầy đủ thông tin bắt buộc');
      }
    },
