    formatDate: function formatDate(date) {
      if (!date) return null;

      var _date$split = date.split('-'),
          _date$split2 = _slicedToArray(_date$split, 3),
          year = _date$split2[0],
          month = _date$split2[1],
          day = _date$split2[2];

      return "".concat(day, "/").concat(month, "/").concat(year);
    },
    parseDateToTimestamp: function parseDateToTimestamp(date) {
      if (!date) {
        return null;
      }

      var _$split = "".concat(date).split('/'),
          _$split2 = _slicedToArray(_$split, 3),
          day = _$split2[0],
          month = _$split2[1],
          year = _$split2[2];

      var day2 = "".concat(year, "-").concat(month.padStart(2, '0'), "-").concat(day.padStart(2, '0'));

      if (new Date(day2) == 'Invalid Date') {
        return '';
      } else {
        return new Date(day2).getTime();
      }
    },
    dateTimeView: function dateTimeView(arg) {
      if (arg) {
        var value = new Date(arg);
        return "".concat(value.getDate().toString().padStart(2, '0'), "/").concat((value.getMonth() + 1).toString().padStart(2, '0'), "/").concat(value.getFullYear(), " | ").concat(value.getHours().toString().padStart(2, '0'), ":").concat(value.getMinutes().toString().padStart(2, '0'));
      } else {
        return '';
      }
    },
